body{
    background-color: black;
}

.Navbar{
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    background-color: transparent;
}
/* background-color: #1a1a1a; */

.navbar-container{
    justify-content: space-between;
    padding: 1rem;
    /* box-shadow: rgb(78, 78, 78) 0px 4px 12px; */
    height: 2rem;
}

.logo{
    cursor: pointer;
    padding-bottom: .5rem;
}

.logo h1{
    display: inline;
}

.list ul li{
    list-style: none;
    padding: 0 1rem;
    cursor: pointer;
    transition: .2s;
}

.list ul li a{
    text-decoration: none;
    color: white;
}

.list ul li a:hover{
    color: orange;
    font-size: 1.1rem;
}

.right-nav{
    width: 50%;
    justify-content: flex-end;
}

.searchbar{
    padding: .5rem;
    width: 70%;
    margin-left: 8rem;
}

.searchbar input{
    width: 100%;
    margin-right: .2rem;
    padding: .3rem;
    padding-left: 1rem;
    border-radius: .3rem;
    border: 1px solid white;
    background-color: transparent;
    color: white;
}

.searchbar input:focus{
    outline: none;
}

.btn{
    padding: .3rem .3rem;
    border-radius: .3rem;
    border: 1px solid rgb(156, 156, 156);
    background-color: aqua;
    color: rgb(50, 50, 50);
    cursor: pointer;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    transition: .3s;
    width: 80px;
}

#login-btn{
    margin-left: 1rem;
}

.btn:hover{
    width: 100px;
    background-color: #3f92fe;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.ham img{
    width: 1.5rem;
}

.ham-nav{
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    animation-name: topDown;
    animation-duration: 1.5s;
}

.ham-nav-div{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem 0;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

#ham-searchbar{
    padding: .5rem 1rem;
    box-shadow: none;
}

#ham-searchbar input{
    width: 100%;
    padding: .5rem 1rem;
    font-size: larger;
}

.ham-nav-div p{
    margin: 0;
}

.ham-nav-div a{
    text-decoration: none;
    color: rgb(78, 78, 78);
    font-size: larger;
}

/* utility classes */
.flex{
    display: flex;
    align-items: center;
}

.hidden{
    display: none;
}

.block{
    display: block;
}

/* Responsive */

@media screen and (max-width:950px) {
    .list,.searchbar input,#search-btn{
        display: none;
    }
    .ham{
        display: block;
    }
    .right-nav{
        width: auto;
    }
    .searchbar{
        padding: 0;
        width: auto;
        margin-left: 0;
    }
    .searchbar input{
        margin: 0;
    }   
    #login-btn{
        margin-left: 0;
    }
}

/* Animation */

@keyframes topDown {
    from{
        transform: translateY(-100%);
    }
    to{
        transform: translateY(0%);
    }
}